@import "https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=Raleway:ital,wght@0,200;0,300;0,400;1,100;1,200&display=swap";
html {
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
  font-size: 62%;
  overflow: hidden;
}

@media only screen and (max-width: 800px) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 600px) {
  html {
    font-size: 43.75%;
  }
}

@media only screen and (max-width: 400px) {
  html {
    font-size: 37.5%;
  }
}

body {
  font-size: 1.6rem;
  position: relative;
}

.game-background {
  position: relative;
}

.main-head {
  color: #2e2d2d;
  text-transform: uppercase;
  text-align: center;
  margin: 4rem;
  font-size: 5rem;
  font-weight: 200;
}

.game-turn {
  text-align: center;
  margin: 4rem;
  font-size: 4rem;
}

.result {
  text-align: center;
  font-size: 4rem;
  font-weight: 200;
}

.game-board {
  height: min(80vh, 45rem);
  width: min(85%, 60rem);
  --border-color: #616060;
  margin: auto auto 10rem;
  position: relative;
}

.boxes {
  height: 100%;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.boxes div {
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 9rem;
}

.controls {
  width: -moz-fit-content;
  width: fit-content;
  z-index: 2;
  background-color: #fff;
  border-radius: 5px;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 200px);
  box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.stick {
  text-align: center;
}

.inputControl {
  margin: 1.5rem;
}

.inputControl input {
  color: #212529;
  appearance: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 4px;
  margin: 0 1rem;
  padding: 6px 12px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.inputControl input:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 .25rem #0d6efd40;
}

.inputControl:last-child {
  flex-direction: row-reverse;
  display: flex;
}

.inputControl:last-child button {
  margin: 0 1rem;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #21252944;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

#click {
  width: 100%;
  height: 3rem;
  border: none;
}

#click:hover {
  background-color: #959da5;
  transition: all .3s;
}

.line {
  width: 100%;
  height: 2px;
  background-color: var(--border-color);
  display: none;
  position: absolute;
}

#line1 {
  top: 15%;
}

#line2 {
  top: 50%;
}

#line3 {
  top: 80%;
}

#line4, #line5, #line6 {
  height: 100%;
  width: 2px;
  top: 0;
}

#line4 {
  left: 16.5%;
}

#line5 {
  left: 49.5%;
}

#line6 {
  left: 83%;
}

#line7 {
  height: 148%;
  width: 2px;
  transform: rotate(-52deg)translate(64rem, -10rem);
}

#line8 {
  height: 148%;
  width: 2px;
  transform: rotate(53deg)translate(-27rem, -60rem);
}

.button {
  appearance: none;
  box-sizing: border-box;
  color: #24292e;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
  white-space: nowrap;
  word-wrap: break-word;
  background-color: #fafbfc;
  border: 1px solid #1b1f2326;
  border-radius: 6px;
  padding: 6px 16px;
  font-family: -apple-system, system-ui, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  list-style: none;
  transition: background-color .2s cubic-bezier(.3, 0, .5, 1);
  display: inline-block;
  position: relative;
  box-shadow: 0 1px #1b1f230a, inset 0 1px #ffffff40;
}

.button:hover {
  background-color: #f3f4f6;
  text-decoration: none;
  transition-duration: .1s;
}

.button:disabled {
  color: #959da5;
  cursor: default;
  background-color: #fafbfc;
  border-color: #1b1f2326;
}

.button:active {
  background-color: #edeff2;
  transition: none;
  box-shadow: inset 0 1px #e1e4e833;
}

#box-1, #box-2 {
  border-right: solid 2px var(--border-color);
  border-bottom: solid 2px var(--border-color);
}

#box-3 {
  border-bottom: solid 2px var(--border-color);
}

#box-4, #box-5 {
  border-right: solid 2px var(--border-color);
  border-bottom: solid 2px var(--border-color);
}

#box-6 {
  border-bottom: solid 2px var(--border-color);
}

#box-7, #box-8 {
  border-right: solid 2px var(--border-color);
}

